@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,500;0,900;1,400&display=swap);
html,body,#root{width:100%;scrollbar-color:#fd50007b rgba(33,33,33,.17);scrollbar-width:thin}html ::-webkit-scrollbar,body ::-webkit-scrollbar,#root ::-webkit-scrollbar{width:4px;height:3px}html ::-webkit-scrollbar-track,body ::-webkit-scrollbar-track,#root ::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(33,33,33,.823);border-radius:2px}html ::-webkit-scrollbar-thumb,body ::-webkit-scrollbar-thumb,#root ::-webkit-scrollbar-thumb{background:#fd500088;border-radius:2px}html ::-webkit-scrollbar-thumb:hover,body ::-webkit-scrollbar-thumb:hover,#root ::-webkit-scrollbar-thumb:hover{background:#fd5000c7}body{margin:0;font-family:"Rubik",sans-serif,-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#191414}
